<template>
    <main>
        <div class="container-fluid">
            <!-- START: Breadcrumbs-->
            <div class="d-block header-section mt-2 mb-3">
                <div>
                    <h1>
                        <b>Change SKU/Product Name View</b>
                    </h1>
                </div>
            </div>
            <div>
            <!-- START: Card Data-->
                <div class="row">
                    <div class="col-12 col-md-12 mt-3">				
                        <div class="card mb-4">
                            <div class="row p-5">
                                <div class="col-12 col-md-4 p-3 row mob-c">
                                    <div class="col-md-3"><img src="@/assets/images/icons/order-one.png" class="mob-img"/></div>
                                    <div class="col-md-9 mt-2">
                                    <h5 class="text-muted">Order Number</h5>
                                    <h5 class="d-flex align-items-left text-primary">{{order.number}}</h5></div>
                                </div>
                                <div class="col-12 col-md-4 border-left p-3 row mob-c">
                                    <div class="col-md-3"><img src="@/assets/images/icons/order-two.png" class="mob-img"/></div>
                                    <div class="col-md-9 mt-2">
                                    <h5 class="text-muted">Order Status</h5>
                                    <h5 class="d-flex align-items-left text-primary">{{order.status}}</h5>
                                    </div>
                                </div>
                                <div class="col-12 col-md-4 border-left p-3 row mob-c">
                                    <div class="col-md-3"><img src="@/assets/images/icons/order-three.png" class="mob-img"/></div>
                                    <div class="col-md-9 mt-2">
                                    <h5 class="text-muted">Manufacturer</h5>
                                    <h5 class="d-flex align-items-left text-primary">{{order.orgName}}</h5>
                                    </div>
                                </div> 
                                <div class="col-12 col-md-4 p-3 row mob-c">
                                    <div class="col-md-3"><img src="@/assets/images/icons/order-four.png" class="mob-img"/></div>
                                    <div class="col-md-9 mt-2">
                                    <h5 class="text-muted">Change Request Type</h5>
                                    <h5 class="d-flex align-items-left text-primary">{{reelChangeType[order.reelChangeType]}}</h5>
                                    </div>
                                </div>
                                <div v-if="order.reelChangeType == 1 || order.reelChangeType == 3" class="col-12 col-md-4 border-left p-3 row mob-c">
                                    <div class="col-md-3"><img src="@/assets/images/icons/order-four.png" class="mob-img"/></div>
                                    <div class="col-md-9 mt-2">
                                    <h5 class="text-muted">Change To SKU</h5>
                                    <h5 class="d-flex align-items-left text-primary">{{order.changeToSkuName}}</h5>
                                    </div>
                                </div>
                                <div v-if="order.reelChangeType == 2 || order.reelChangeType == 3" class="col-12 col-md-4 border-left p-3 row mob-c">
                                    <div class="col-md-3"><img src="@/assets/images/icons/order-five.png" class="mob-img"/></div>
                                    <div class="col-md-9 mt-2">
                                    <h5 class="text-muted">Change To Product Name</h5>
                                    <h5 class="d-flex align-items-left text-primary">{{order.changeToProductName}}</h5>
                                    </div>
                                </div>
                                <div class="col-12 col-md-4 border-left p-3 row">
                                    <h5 class="d-flex align-items-left text-primary"> </h5>
                                </div> 
                            </div>
                        </div>

                        <div class="card">
                            <div class="table-responsive">
                            <table id="tab" class="table order">
                                <thead>
                                    <tr>
                                        <th>code</th>
                                        <th>Old Product Name</th>
                                        <th>Old SKU</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr v-for="orderItem in order.reelChangeRequestReels" :key="orderItem.id">
                                        <td>{{orderItem.reelCode}}</td>
                                        <td>{{orderItem.productName}}</td>
                                        <td>{{orderItem.sku}}</td>
                                    </tr>
                                </tbody>
                            </table>
                            </div>
                        </div>
                            <!-- START: Card Data-->
                            <div class="row">
                            <div class="col-12 col-sm-12 mt-3">
                                <p class="text-muted mb-2">History</p>
                                <div class="table-responsive">
                                    <table class="table layout-secondary bordered">
                                        <thead>
                                            <tr>
                                                <th>Date</th>
                                                <th>User Type</th>
                                                <th>Action</th>
                                                <th>Note</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr style="background: #fff;" v-for="history in order.reelChangeRequestHistories" :key="history.id">
                                                <td>{{history.modifiedDate}}</td>
                                                <td>{{history.actionnedBy}}</td>
                                                <td>{{history.action}}</td>
                                                <td>{{history.comments}}</td> 
                                            </tr>
                                        </tbody>
                                    </table> 
                                </div>
                            </div>
                        
                        </div>
                        <!-- END: Card DATA-->
                        
                    </div>
                </div>
            <!-- END: Card DATA-->
            </div>
            <div class="mb-2">
                <b-button v-b-modal.modal-sm
                    class="float-right mt-2 mb-3 p-2 pl-5 pr-5"
                    variant="outline-primary"
                    @click="redirectToHome"
                >
                    CANCEL
                </b-button>
            </div>
            <!-- END: Card DATA-->
        </div>
    </main>
</template>

<script>
import axios from "axios";
export default {

    data: () => ({
        order: {},
        reelChangeType: {
            1: "Change SKU",
            2: "Change Product Name",
            3: "Both"
        }
    }),
    props: ['requestId'],
    mounted() {
        axios.get("/ReelChange", {
            baseURL: process.env.VUE_APP_OrderMgmtApisBaseUrl,
            params: { requestId: this.requestId }
        }).then((response) => {
            this.order = response.data;
            console.log(response.data);
        });
    },
    computed: {
    },
    methods: {
        redirectToHome() {
            this.$router.push({ name: 'ChangeSKUProductNamelisting' });
        },
    },
    watch: {
      selectedCodes(newVal) {
        // Handle changes in individual flavour checkboxes
        if (newVal.length === 0) {
          this.indeterminate = false
          this.selectAll = false
        } else if (newVal.length === this.codes.length) {
          this.indeterminate = false
          this.selectAll = true
        } else {
          this.indeterminate = true
          this.selectAll = false
        }
      }
    }
};
</script>